import React, { useState } from 'react'
import { Calendar as MyCalendar, dateFnsLocalizer, Views } from 'react-big-calendar'
import format from 'date-fns/format'
import parse from 'date-fns/parse'
import startOfWeek from 'date-fns/startOfWeek'
import getDay from 'date-fns/getDay'
import DatePicker from "react-datepicker"
import "react-big-calendar/lib/css/react-big-calendar.css"
import "react-datepicker/dist/react-datepicker.css"


const locales = {
    "en-US": require("date-fns/locale/en-US")
}

const localizer = dateFnsLocalizer({
    format,
    parse,
    startOfWeek,
    getDay,
    locales
})

const events = [
    
]


const Calendar = () => {

    const [newEvent, setNewEvent] = useState({ title: "", start: "", end: "" })
    const [allEvents, setAllEvents] = useState(events)

    const handleAddEvent = () => {
        setAllEvents([...allEvents, newEvent])
    }

    console.log(newEvent.start)

    // console.log(document.getElementById("startDate").value)

    return (
        <>
            <nav className="navbar">
                <div className="container">
                    <a className="navbar-brand">Timeline</a>
                    <a className="btn btn-primary" data-bs-toggle="offcanvas" href="#offcanvasExample" role="button" aria-controls="offcanvasExample">
                        Add Event
                    </a>
                    <div className="offcanvas offcanvas-end" tabIndex="-1" id="offcanvasExample" aria-labelledby="offcanvasExampleLabel">
                        <div className="offcanvas-header">
                            <h5 className="offcanvas-title" id="offcanvasExampleLabel">Add Event</h5>
                            <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"></button>
                        </div>
                        <div className="offcanvas-body">
                            <div>
                                <div className="mb-3">
                                    <label htmlFor="textInputEmail1" className="form-label">Title</label>
                                    <input type="text" className="form-control" id="textInputEmail1" value={newEvent.title}
                                        onChange={(e) => setNewEvent({ ...newEvent, title: e.target.value })}
                                    />
                                </div>
                                <div className="mb-3">
                                    <label htmlFor="startDate" className="form-label">Start Date</label>
                                    <DatePicker showTimeSelect dateFormat="MMMM d, yyyy h:mm aa" className="form-control" id="startDate"
                                        selected={newEvent.start} onChange={(start) => setNewEvent({ ...newEvent, start })} />
                                </div>
                                <div className="mb-3">
                                    <label htmlFor="endDate" className="form-label">End Date</label>
                                    <DatePicker showTimeSelect dateFormat="MMMM d, yyyy h:mm aa" className="form-control" id="endDate"
                                        selected={newEvent.end} onChange={(end) => setNewEvent({ ...newEvent, end })} />
                                </div>
                                <button className="btn btn-primary" data-bs-dismiss="offcanvas" aria-label="Close" onClick={handleAddEvent}>Add Event</button>
                            </div>
                        </div>
                    </div>
                </div>
            </nav>
            <div>
                <MyCalendar localizer={localizer}
                    events={allEvents}
                    defaultView={Views.WEEK}
                    startAccessor="start"
                    endAccessor="end"
                    style={{ height: 500, margin: "50px" }}
                />
            </div>
        </>
    )
}

export default Calendar;